:root{
  --show-contenido-width:0px;
  --show-tema-width:0px;
  --z-contactos:1;
}

.contactos-div{
    z-index: 1;
    z-index: var(--z-contactos);
    visibility: var(--show);
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: var(--color-div-contact); 
    transition: 250ms;
