    transition-duration: 500ms; 
    font-family: 'Raleway', sans-serif;
}
.contacto-div-interno{
    visibility: var(--show);
    width: var(--width-div-interno-contact);
    height: var(--height-div-interno-contact);
    left: 15vw;
    top: 10vh;
    z-index: 10;
    position: fixed;
    background-color: rgb(255, 255, 255);
    border-radius: 12px;
}
.tema-contactos{
    visibility: var(--show);
    width: 0px;
    width: var(--show-tema-width);;
    height: var(--show-tema-height);
    background-color: rgba(0, 255, 255, 0);
    color: rgb(0, 140, 215);
    font-family: 'Raleway', sans-serif;
    z-index: -1;
    font-size: 4vw;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4%;
}
.contactos-contenido{
    width: 0px;
    width: var(--show-tema-width);
    height: 100%;
    background-color: rgb(255, 255, 255);    
}
.sub-contactos-contenido-up{
    width: 0px;
    width: var(--show-tema-width);
    height: 10vh;
    background-color: rgb(0, 140, 215);  
    font-size: 2vw;
}
.t-icono{
    width: 0px;
    width: var(--show-tema-width);
    height: auto;
    background-color: rgba(137, 43, 226, 0);
    color: rgb(255, 255, 255);
    padding-top: 3vh;
}
.sub-contactos-contenido-down{
    width: 0px;
    width: var(--show-tema-width);
    height: 80%;
    background-color: rgba(215, 0, 32, 0);  
}
.contactos-down{
    width: 0px;
    width: var(--show-tema-width);
    height: 100%;
    background-color: rgba(0, 255, 255, 0);
}
.contactos-down-img{
    width: 100%;
    height: auto;
    background-color: rgba(0, 255, 8, 0);
    display: flex;
    justify-content: center;
    align-items: center;
}
.contactos-down-img img{
    width: 45vh;
    height: auto;
    background-color: rgba(255, 187, 0, 0);  
}
.contactos-down-a{
    width: 100%;
    height: auto;
    background-color: rgba(0, 17, 255, 0);
}
.contactos-down-a a{
    text-decoration: none;
    color: rgb(0, 140, 215);
    font-size: 1.7vw;
    text-shadow: 0.01vw 0.01vw rgba(0, 29, 44, 0.909);
    background-color: rgba(127, 255, 212, 0);
}
.contactos-down-a p{
    background-color: rgba(127, 255, 212, 0);
    text-decoration: none;
    color: rgb(0, 140, 215);
    font-size: 1vw;
    text-shadow: 0.01vw 0.01vw rgba(0, 29, 44, 0.909);
}

a:hover{
    -webkit-animation: shake 1s;
            animation: shake 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite; 
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
    0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}
