* {
    margin: 0;
    padding: 0px;
    overflow: hidden
}
:root{
  --show-interiorismo:'hidden';
  --color-contenedor-interiorismo:rgba(0,0,0,0);
  --width-contenedor-interno-interiorismo:0vw;
  --height-contenedor-interno-interiorismo:0vh;
  --z-interior:1;
}
.contenedor-interiorismo{
    visibility: 'hidden';
    visibility: var(--show-interiorismo);
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 1;
    z-index: var(--z-interior);
    background-color: rgba(0,0,0,0);
    background-color: var(--color-contenedor-interiorismo); 
    transition: 250ms;
    transition-delay: 250ms;
    transition-duration: 500ms; 
    font-family: 'Raleway', sans-serif;
}
.contenedor-interno{
    width: 0vw;
    width: var(--width-contenedor-interno-interiorismo);
    height: 0vh;
    height: var(--height-contenedor-interno-interiorismo);
    left: 5vw;
    top: 20vh;
    z-index: 10;
    position: fixed;
    background-color: rgb(255, 255, 255,0);
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.diseno-interior, .muebleria, .luminaria{
    width: 33.3333%;
    height: 100%;
    background-color: rgba(0, 255, 255, 0);
    margin: 0%;
}
.inter-video{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 255, 0);
    transition: 0ms;
    transition-delay: 0ms;
    transition-duration: 0ms; 
}
.video-luminaria video, .video-muebles video, .video-interior video{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 255, 145, 0);
    object-fit: cover;
}
.inter-p{
    width: 100%;
    height: 100%;
    background-color: rgba(255, 5, 5, 0);
    color: aliceblue;
    z-index: 800;
    font-size: 2vw;
    margin-top: -80%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.inter-p a{
    width: 100%;
    height: auto;
    background-color: rgba(216, 112, 147, 0);
    color: aliceblue;
    font-size: 2.5vw;
    text-decoration: none;
    text-shadow: 1px 1px rgba(0, 0, 0, 1);
    z-index: 9;
}

.video-fondo-inter video{
    position: absolute;
    width: 100vw;
    height: 100vh;
    z-index: 8;
    object-fit: cover;
    background-color: rgba(0, 0, 0, 0);
    left: 0px;
}
